import React from 'react'
import './SwipeButtons.css'
import ReplayIcon from '@material-ui/icons/Replay'
import CloseIcon from '@material-ui/icons/Close'
import StarRateIcon from '@material-ui/icons/StarRate'
import FavoriteIcon from '@material-ui/icons/Favorite'
import FlashOnIcon from '@material-ui/icons/FlashOn'
import { IconButton } from '@material-ui/core'
function SwipeButtons() {
    return (
        <div class = "swipeButtons">
            
            
            
            
            <IconButton className = "each__button swipeButtons__repeat">
            <ReplayIcon fontSize ="large"></ReplayIcon>
            </IconButton>
            <IconButton className = "each__button swipeButtons__left">
            <CloseIcon fontSize ="large"></CloseIcon>
            </IconButton>
            <IconButton className = "each__button swipeButtons__star">
            <StarRateIcon fontSize ="large"></StarRateIcon>
            </IconButton>
            <IconButton className = "each__button swipeButtons__right">
            <FavoriteIcon fontSize ="large"></FavoriteIcon>
            </IconButton>
            <IconButton className = "each__button swipeButtons__lightning">
            <FlashOnIcon fontSize ="large"></FlashOnIcon>
            </IconButton>

        </div>
    )
}

export default SwipeButtons
